import Sortable from 'sortablejs';

const WbPo = window.WbPo || {};

/**
 * Set the selected the items in fields.
 *
 * @since 1.0.0
 */
function setSelectedItems() {
	const products = [];
	if (document.querySelectorAll('#wbpo_selected li').length) {
		document.querySelectorAll('#wbpo_selected li').forEach((item) => {
			const ID = item.dataset.id;
			const qty = item.querySelector('.qty input').value.trim();
			const tag = item.querySelector('#wbpo_tag_text').value.trim();
			const offer = item.querySelector('#wbpo_offer_text').value.trim();
			const belowOffer = item.querySelector('#wbpo_below_offer_text').value.trim();
			const aboveVariation = item.querySelector('#wbpo_above_variation_text').value.trim();
			products.push({ id: ID, qty, tag, offer, belowOffer, aboveVariation });
		});

		document.getElementById('wbpo_current_group').value = JSON.stringify(products);
	}
}

/**
 * Quantity change on Backend.
 *
 * @since 1.0.0
 */
function qtyChangeListener() {
	if (document.querySelectorAll('.wbpo-search-products .qty > input').length) {
		document.querySelectorAll('.wbpo-search-products .qty > input').forEach((item) => {
			item.addEventListener('change', () => {
				setSelectedItems();
			});
		});
	}
}

/**
 * Remove group Product item from list.
 *
 * @since 1.0.0
 */
function removeGpItem() {
	if (document.querySelector('.wbpo-search-products li .remove')) {
		document.querySelectorAll('.wbpo-search-products li .remove').forEach((item) => {
			item.addEventListener('click', (e) => {
				e.target.closest('li').remove();
				setSelectedItems();
			});
		});
	}
}

/**
 * More Info field change save value.
 *
 * @since 1.0.0
 */
function moreInfoChangeListener() {
	if (document.querySelector('.wbpo-search-products .more-info')) {
		document
			.querySelectorAll('.wbpo-search-products .wbpo-more-detail input')
			.forEach((input) => {
				input.addEventListener('keyup', () => {
					setSelectedItems();
				});
			});
	}
}


/**
 * Show/Hide more info details.
 *
 * @param {object} e event of the listener.
 */
function showMoreInfoDetail( e) {
	const productList = e.target.closest('li');
	const moreInfoSection = productList.querySelector('.wbpo-more-detail');
	if (e.target.classList.contains('close')) {
		e.target.classList.replace('close', 'open');
		moreInfoSection.classList.remove('hidden');
	} else {
		e.target.classList.replace('open', 'close');
		moreInfoSection.classList.add('hidden');
	}
}

/**
 * More info open/close fields.
 *
 * @since 1.0.0
 */
function moreInfoGpItem() {
	if (document.querySelector('#wbpo_selected .more-info')) {
		document.querySelectorAll('#wbpo_selected .more-info').forEach((item) => {
			item.removeEventListener('click', showMoreInfoDetail);
			item.addEventListener('click', showMoreInfoDetail);
		});
	}
}

/**
 * Get Search result for product.
 *
 * @since 1.0.0
 */
function WbPoSearchRequest() {
	const formData = new FormData();
	formData.append('action', 'wbpo_search_products');
	formData.append('search_input', document.querySelector('#wbpo_search_input').value);
	formData.append('current_group', document.querySelector('#wbpo_current_group').value);
	document.querySelector('#wbpo_search_input').classList.add('loading-input');
	fetch(WbPo.ajaxUrl, { method: 'POST', body: formData })
		.then((res) => res.json()) // parse response as JSON (can be res.text() for plain response)
		.then((response) => {
			document.querySelector('#wbpo_search_input').classList.remove('loading-input');
			if (response.data.data) {
				document.getElementById('wbpo-results').innerHTML = response.data.data;
				document.getElementById('wbpo-results').classList.remove('hidden');
			}
			if (response.success) {
				if (document.querySelectorAll('#wbpo-results li ')) {
					document.querySelectorAll('#wbpo-results li').forEach((item) => {
						item.querySelector('.add').addEventListener('click', (e) => {
							const productItem = e.target.parentElement.parentElement;

							productItem.querySelector('.add').innerHTML = '×';
							productItem.querySelector('.add').classList.add('remove');
							productItem.querySelector('.add').classList.remove('add');
							document.querySelector('#wbpo_selected ul').append(productItem);
							document.getElementById('wbpo-results').classList.add('hidden');
							setSelectedItems();
							qtyChangeListener();
							removeGpItem();
							moreInfoGpItem();
							moreInfoChangeListener();
						});
					});
				}
			}
		})
		.catch((err) => {
			console.log(`sorry,  Some issue in search ${err} `); // eslint-disable-line
		});
}

/**
 * Hide and show qty input.
 *
 * @since 1.0.0
 * @param  _hide hide or show.
 */
function WbPoQtyInputVisibility(_hide = true) {
	if (_hide) {
		document.querySelectorAll('.main-content .qty').forEach((item) => {
			item.classList.remove('hidden');
		});
	} else {
		document.querySelectorAll('.main-content .qty').forEach((item) => {
			item.classList.add('hidden');
		});
	}
}
// eslint-disable-next-line @wordpress/no-global-event-listener
document.addEventListener('DOMContentLoaded', () => {
	if( document.getElementById('sortlist') ) {
		Sortable.create(document.getElementById('sortlist'), {
			handle: '.move', // 'filtered' class is not draggable
			animation: 150,
			onEnd: () => {
				setSelectedItems();
			},
		});
	}
	qtyChangeListener(); // Qty change setSelected items.
	removeGpItem(); // Remove item from selected List.
	moreInfoGpItem(); // Open/close more info section.
	moreInfoChangeListener(); // More info fields change listner.
	const SEARCH_INPUT = document.querySelector('#wbpo_search_input');
	if (SEARCH_INPUT) {
		let WbPoTimeout = '';
		SEARCH_INPUT.addEventListener('keydown', () => {
			if (SEARCH_INPUT.value !== '') {
				if (WbPoTimeout !== null) {
					clearTimeout(WbPoTimeout);
				}
				WbPoTimeout = setTimeout(WbPoSearchRequest, 300);
			}
		});
	}

	if (document.getElementById('wbpo_selection_type')) {
		document.getElementById('wbpo_selection_type').addEventListener('change', (e) => {
			if (e.target.value === 'qty') {
				WbPoQtyInputVisibility();
			} else {
				WbPoQtyInputVisibility(false);
			}
		});
	}

	if (
		document.getElementById('wbpo_selection_type') &&
		document.getElementById('wbpo_selection_type').value === 'qty'
	) {
		WbPoQtyInputVisibility();
	} else {
		WbPoQtyInputVisibility(false);
	}
});
